}

.logo-light {
  display: none;
}

/* Search */
.app-search {
  padding: calc(32px / 2) 0;
}
.app-search .input-group {
  background-color: #232838;
  border-radius: 30px;
}
.app-search .input-group .btn {
  color: #919bae;
  background-color: #1f2431;
}
.app-search .input-group .btn:focus {
  box-shadow: none;
}
.app-search .form-control {
  border: none;
  height: 38px;
  padding-left: 20px;
  padding-right: 40px;
  background-color: #2a3042;
  box-shadow: none;
}
.app-search span {
  position: absolute;
  z-index: 10;
  font-size: 16px;
  line-height: 38px;
  right: 13px;
  top: 0;
  color: #919bae;
}

.megamenu-list li {
  position: relative;
  padding: 5px 0px;
}
.megamenu-list li a {
  color: #79859c;
}

