// 
// _header.scss
// 

#page-topbar {
    position: fixed;
    top: 0;
    right: 0;
    left: 0;
    z-index: 1002;
    background-color: $header-bg;
    box-shadow: $box-shadow;
}

.navbar-header {
    display: flex;
    -ms-flex-pack: justify;
    justify-content: space-between;
    align-items: center;
    margin: 0 auto;
    height: $header-height;
    padding: 0 calc(#{$grid-gutter-width} / 2) 0 0;

    .dropdown {
        .show.header-item {
            background-color: darken($header-bg, 2%);
        }
    }
}

.navbar-brand-box {
    padding: 0 1.5rem;
    text-align: center;
    width: $navbar-brand-box-width;
}

.logo {
    line-height: 70px;
    .logo-sm {
        display: none;
    }
}

.logo-light {
    display: none;
}

/* Search */

.app-search {
    padding: calc(#{$header-height - 38px} / 2) 0;

    .input-group{
        background-color: darken($header-bg, 2%);
        border-radius: 30px;

        .btn {
            color: $header-item-color;
            background-color: darken($header-bg, 4%); //#e7e7e7
            &:focus{
                box-shadow:none;
            }
        }
    }
   
    .form-control {
        border: none;
        height: 38px;
        padding-left: 20px;
        padding-right: 40px;
        background-color: $topbar-search-bg;
        box-shadow: none;
    }
    span {
        position: absolute;
        z-index: 10;
        font-size: 16px;
        line-height: 38px;
        right: 13px;
        top: 0;
        color: $gray-600;
    }
}

// Mega menu

.megamenu-list {
    li{
        position: relative;
        padding: 5px 0px;
        a{
            color: $dropdown-color;
        }
    }
}

@media (max-width: 991px) {
    .navbar-brand-box {
        width: auto;
    }
    .logo {
        span.logo-lg {
            display: none;
        }

        span.logo-sm {
            display: inline-block;
        }
    }
}

.page-content {
    padding: calc(#{$header-height} + #{$grid-gutter-width}) calc(#{$grid-gutter-width} / 2) $footer-height calc(#{$grid-gutter-width} / 2);
}

.header-item {
    height: $header-height;
    box-shadow: none !important;
    color: $header-item-color;
    border: 0;
    border-radius: 0px;
    padding: 6px;

    &:hover {
        color: $header-item-color;      
}
.top-icon{
    i{
    color: $header-item-color;
    border: 1px solid lighten($header-item-color, 10%); //#d4d5d9 //#5f757f
    border-radius: 50%;
    width: 34px;
    height: 34px;
    line-height: 32px;
    display: inline-block;
}
}
}

.header-profile-user {
    height: 36px;
    width: 36px;
    background-color: rgba($white, 0.25); //$gray-300
    padding: 3px;
}

.user-dropdown{
    .dropdown-item{
        i{
            display: inline-block;
        }
    }
}
.noti-icon {
    i {
        font-size: 18px;
        color: $header-item-color;
    }

    .badge {
        position: absolute;
        top: 12px;
        right: 4px;
        font-size: 10px;
    }
}


.notification-item {
    .d-flex {
        padding: 0.75rem 1rem;

        &:hover {
            background-color: $gray-300;
        }
    }
}

// Dropdown with Icons
.dropdown-icon-item {
    display: block;
    border-radius: 3px;
    line-height: 34px;
    text-align: center;
    padding: 15px 0 9px;
    display: block;
    border: 1px solid transparent;
    color: $gray-600;

    img {
        height: 24px;
    }

    span {
        display: block;
        overflow: hidden;
        text-overflow: ellipsis;
        white-space: nowrap;
    }

    &:hover {
        border-color: $border-color;
    }
}

// Full Screen
.fullscreen-enable {
    [data-toggle="fullscreen"] {
        .mdi-fullscreen:before {
            content: "\F0294";
        }
    }
}

body[data-topbar="dark"] {
    #page-topbar { 
        background-color: $header-dark-bg; //#242836
    }
    .navbar-header {
        .dropdown {
            .show.header-item {
                background-color: rgba($white, 0.05);
            }
        }

        .waves-effect .waves-ripple {
            background: rgba($white, 0.4);
        }
    }

    .header-item {
        color: $header-dark-item-color;
        padding: 6px;
        .top-icon{
            i{
                border: 1px solid lighten($header-dark-bg, 6%); //#4b5266 #5f757f
            }
        }
    
        &:hover {
            color: $header-dark-item-color;
        }
    }

    .header-profile-user {
        background-color: rgba($white, 0.25);
    }
    
    .noti-icon {
        i {
            color: $header-dark-item-color;
        }
    }

    .logo-dark {
        display: none;
    }

    .logo-light {
        display: block;
    }

    .app-search {
        .input-group{
            background-color: $topbar-search-bg;
            border-radius: 30px;

            .btn {
                color: $header-dark-item-color;
                background-color: $topbar-search-dropdown-bg;
                &:focus{
                    box-shadow:none;
                }
            }
        }
    }
}


body[data-sidebar="dark"] {
    .navbar-brand-box {
        background: $sidebar-dark-bg;
    }
    .logo-dark {
        display: none;
    }
    .logo-light {
        display: block;
    }
}

@media (max-width: 600px) {
    .navbar-header {
        .dropdown {
            position: static;

            .dropdown-menu {
                left: 10px !important;
                right: 10px !important;
            }
        }
    }
}

@media (max-width: 380px) {
    .navbar-brand-box {
        display: none;
    }
}

body[data-layout="horizontal"] {
    .navbar-brand-box {
        width: auto;
    }
    .page-content {
        margin-top: $header-height;
        padding: calc(55px + #{$grid-gutter-width}) calc(#{$grid-gutter-width} / 2) $footer-height calc(#{$grid-gutter-width} / 2);
    }    
}

@media (max-width: 992px) { 
    body[data-layout="horizontal"] {
        .page-content {
            margin-top: 15px;
        }    
    }
}


